@import '_Variabless.scss';


.list__movie {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    justify-content: space-between;

    background-color: #3b3b3b;


    // border: 1px solid rgba(255, 255, 255, 0.18);

    &.list__fav {
        background: rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        border-radius: 10px;
    }

    &-poster {
        width: 70px;
    }

    &--description {
        text-align: center;
        flex-grow: 1;
        font-size: 1.2rem;

        p:nth-child(1) {
            font-size: 1.5rem;
            font-weight: bold;
        }
    }

    &--btn {

        button {
            background-color: rgb(92, 85, 232);
            padding: 5px;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            color: white;
            font-weight: bold;
        }

        &-msg {
            position: absolute;

            z-index: 5;
            animation: showIsExist 0.2s ease both;
            transition: all 0.2s;
            background: rgba(255, 255, 255, 0.25);
            box-shadow: 0 8px 32px 0 rgba(19, 19, 19, 0.37);
            backdrop-filter: blur(4px);
            border-radius: 5px;
            border: 1px solid rgba(255, 255, 255, 0.18);
            padding: 2px 10px;
            font-weight: bold;
            font-size: 1.2rem;

            @keyframes showIsExist {
                from {
                    opacity: 0;
                    top: 0%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }

                to {
                    opacity: 100;
                    top: 15%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
        }


    }
}