@import '../styles/Variabless';



.list__search--movie {


    &-form {
        margin: 10px 0;
        color: white;
    }

    &-input {
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        border-radius: 10px;
        color: white;
        padding: 10px;
        color: white;
        outline: none;
        border: none;
    }


    &-list {
        position: relative;
        max-height: 300px;
        overflow: auto;
        overflow-x: hidden;
    }
}