//mobile

.row {

    &__arrow {
        display: flex;

        &--scroll {
            display: none;
            align-items: center;
            justify-content: center;
            margin: 0 5px;
            background-color: rgba(60, 60, 60, 0.374);
            cursor: pointer;
            margin: 20px 0;
        }

        &--btn {
            flex-grow: 1;
            width: 30px;
            height: 20px;
        }
    }

    &__posters {
        display: flex;
        overflow-x: scroll;
        overflow-y: hidden;
        padding: 20px;
        scroll-behavior: smooth;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__poster {
        max-height: 100px;
        margin-right: 10px;
        transition: transform 450ms;

        &:hover {
            transform: scale(1.08);
        }

        &Large {
            max-height: 250px;
        }
    }
}


//desktop 

@media screen and (min-width: 1023px) {
    .row {
        &__arrow {
            &--scroll {
                display: flex;
            }
        }
    }
}